import React, { Component } from 'react';
import classNames from 'classnames';
import Slider from 'react-slick';

import './styles.scss';

export class CustomCarousel extends Component {
  static defaultProps = {
    height: '100%',
    width: '100%',
    className: '',
  };

  constructor(props) {
    super(props);

    this.state = {
      items: this.convertItems(props.items),
    };
  }

  convertItems(items) {
    return items.map((item, i) => ({
      src: item,
      active: i === 0,
    }));
  }

  render() {
    const { height, width, className, items } = this.props;

    const settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      fade: true,
    };

    console.log(
      items.map(({ src, active }, i) => (
        { backgroundImage: `url('${src}')` }
      ))
    );

    return (
      <Slider {...settings}>
        {items.map((src, i) => (
          <img
            key={i}
            src={src}
            alt="Slide"
          />
        ))}
      </Slider>
    )
  }
}

export default CustomCarousel;
